import React, { memo, useState } from 'react';
import { ChevronDown, ChevronUp } from 'lucide-react';

const InfoSection = memo(({ 
  title, 
  icon: Icon, 
  children, 
  description,
  defaultCollapsed = false,
  className = '' 
}) => {
  const [isCollapsed, setIsCollapsed] = useState(defaultCollapsed);

  return (
    <section 
      className={`
        pb-6 border-b border-green-100/60 last:border-b-0
        ${className}
      `}
    >
      <button
        onClick={() => setIsCollapsed(!isCollapsed)}
        className="
          w-full flex items-center justify-between 
          text-lg font-semibold text-green-900 hover:text-green-800 
          transition-colors focus:outline-none focus:ring-2 
          focus:ring-green-500 focus:ring-offset-2 rounded-lg
          py-3 group
        "
      >
        <div className="flex items-center gap-3">
          {Icon && (
            <Icon className="w-5 h-5 text-green-700 group-hover:text-green-600 transition-colors" />
          )}
          <span className="tracking-wide">{title}</span>
        </div>
        {isCollapsed ? (
          <ChevronDown className="w-5 h-5 text-green-600 group-hover:text-green-500 transition-colors" />
        ) : (
          <ChevronUp className="w-5 h-5 text-green-600 group-hover:text-green-500 transition-colors" />
        )}
      </button>
      
      {!isCollapsed && (
        <div className="mt-4 space-y-3 text-green-950 text-sm leading-relaxed">
          {description && (
            <p className="text-green-800 text-sm mb-4 pb-3 border-b border-green-100/40">
              {description}
            </p>
          )}
          <div className="space-y-2">
            {children}
          </div>
        </div>
      )}
    </section>
  );
});

export default InfoSection; 